<template>
  <div class="ditribute_case">
    <listSearch 
      v-model="searchFormData"
      @searchConfirm="goSearch"
      :searchArr="searchArr">
    </listSearch>
    <div class="content">
      <div class="buttons">
        <Button type="primary" @click="btnOperate(2)">批量分配</Button>
      </div>
      <div class="table_box mgt20">
        <Table
          @on-select="selectOne"
          @on-select-cancel="selectCancel"
          @on-select-all="selectAll"
          @on-select-all-cancel="selectAllCancel"
          :columns="columns"
          :data="tableData"
          show-sizer
          show-elevator
          show-total>

          <template  slot-scope="{ row }"  slot="action">
            <div>
              <a href="javascript:;" @click="btnOperate(1,row)" v-if="row.pullFlag=='未分配'">
                 <img width="22" height="22" src="../../../assets/images/fenpei.png" alt="" title="分配">
              </a>
                <a href="javascript:;" @click="btnOperate(3,row)" v-else style="margin-left:10px" >
                 <img width="22" height="22" src="../../../assets/images/chongxinfenpei.png" alt="" title="重新分配">
                 </a>
          
            </div>
          </template>
        </Table>
        <!-- 分页 -->
       <Page
          :current="currentPage"
          :total="total"
          :page-size="pageSize"
          :page-size-opts="pageOpts"
          @on-change="changeCurrentPage"
          @on-page-size-change="changeSize"
          show-sizer
          show-elevator
          show-total
        />
      </div>
    </div>
    <!-- 弹窗部分 -->
    <popupBox :show="popupShow" 
      :titleText="popupTypeObj[popupType]['title']"
      :width="'740'"
      @popupConfirm="popupConfirm" 
      @popupCancel="popupCancel"
      >
      <!-- 分配仲裁员 -->
      <!-- 仲裁员 -->
      <div class="select_content" v-if="popupType == 1 || popupType == 2">
        <div class="info">
          当前被分配人员：{{distributedPerson}}
        </div>
        <div class="header_box">
          <div class="title">选择仲裁员</div>
            <Input
              prefix="ios-search"
              v-model="selectPerson"
              placeholder="姓名或首字母"
              style="width:200px"
            />
        </div>
        <Table  class="file_table" :columns="personColumns" :data="personTable">
          <template slot-scope="{ row,index}" slot="action">
            <Checkbox v-model="row.checked" @on-change="handleCheckbox(row,index)"></Checkbox>
          </template>
        </Table>
        <Page :current="1" :total="personTable.lenght" :page-size="personTable.lenght" />
      </div>
    </popupBox>
  </div>
</template>

<script>
import listSearch from '@/components/list-search/search.vue'
import {listMixin} from '../listMixin'
import { mapActions } from 'vuex'
const columns = [
        {
          type: 'selection',
          width: 100,
          align: 'center',
        },
        {
          title: '案号',
          key: 'code',
          align: "center",
          width:200,
        },
        {
          title: '申请人',
           key: 'applicantName',
          align: "center"
        },
        {
          title: '代理人',
          key: 'proxyName',
          align: "center"
        },
        {
          title: '被申请人',
           key: "respondentName",
          align: "center"
        },
        {
          title:"仲裁费",
          key:"cost",
          align:'center'
        },
        {
          title: '案件类型',
          key: 'caseTypeName',
          align: "center"
        },
        {
          title:"提交时间",
          key:"createTime",
          align:'center',
          width:200
        },
        {
          title: '受理时间',
           key: 'acceptTime',
           align: "center",
           width:200
        },
        {
            title:"接收人",
            key:'secretaryName',
            align:"center"
        },
        {
          title:"状态",
          key:"pullFlag",
          align:"center"
        },
        {
          title: '操作',
          slot: 'action'
        }
      ]
export default {
  name: 'ditribute_case',
  components: { listSearch },
  mixins: [listMixin],
  data () {
    return {
      popupShow: false,
      popupType: 1,
      popupTypeObj: {
        1: {type:1, title: '分配仲裁员'},
        2: {type:2, title: '批量分配仲裁员'},
      },
      distributedPerson: '暂无人员分配', // 被分配人员
      selectPerson: '', //选择仲裁员
      acceptList: ['hahah',2],
      rejectList: [1,2],
      rejectReason: null,
      disputeType: null,
      searchFormData: {
        personalName: null,
        companyName: null,
        checkbox: [],
        radio: null,
        status: null
      },
      searchArr: [
        {type: 'input', value: 'companyName', label: '申请人', placeholder: '请输入企业名称'},
        {type: 'input', value: 'companyName', label: '被申请人', placeholder: '请输入企业名称'},
        {type: 'select', value: 'status', label: '注册名称',
         arr: [
          {label: '通过', value: 1},
          {label: '未通过', value: 2}
         ], 
          placeholder: '请选择'
        },
        {type: 'select', value: 'status', label: '案件类型',
         arr: [
          {label: '通过', value: 1},
          {label: '未通过', value: 2}
         ], 
          placeholder: '请选择'
        }
      ],
      columns: columns,
      personColumns:[{title:'姓名',key:"username"},{title:"专业",key:"roleName"},{title:"选择",slot:"action"}],
      personTable:[{name:'张三1',checked:false},{name:'张三2',checked:false}],
      requsetIds:''
    }
  },
  mounted () {
    this.getAllPerson()
  },
  methods: {
    ...mapActions({
      getRequsetList:"createCase/getRequsetList",
      setDistribution:"replyPeriod/setDistribution",
      gelAllArbitrator:"replyPeriod/gelAllArbitrator",
      setDistribuAll:"replyPeriod/setDistribuAll"
    }),
    // 搜索
    goSearch () {
      console.log(this.searchFormData)
    },
    // 按钮此作
    btnOperate (type,row) {
      // 1:分配 2 重新分配  3 批量分配
      console.log(type)
      if(type==1){
        this.requsetIds=row.id;
      }
 
         
         if (type==2){
           let length = this.selectedData.length;
           if (length === 0) {
              this.$Message.warning("请至少选择一条案件信息");
              return;
          }
         }
          
      this.popupType = type
      this.popupShow = true
    },
    // 弹窗确认
    async popupConfirm () {
      if(this.popupType==1) {
        // 单个分配
        this.setDistribOne()

      } else if(this.popupType==2){
        this.setDistribAll()
      }
      
    },
    async setDistribAll(){
      let params = {
        entity_caseId:'',
        second_secretary:'',
        second_secretary_name:"",
        actionIdentifer:'CASE_ASSIGN_BATCH'
        }
        const arr = this.personTable.filter(item=>{
            if(item.checked){
              return item
            } else {
              return 
            }
            })
      if(arr.length>0){
        params.second_secretary= arr[0].id
         params.second_secretary_name= arr[0].nickname
      }
      let request = []
      this.selectedData.map(item=>{
        let obj = JSON.parse(JSON.stringify(params))
        obj.entity_caseId=item
        request.push(obj)
      })
      console.log(request,'参数')
      const result = await this.setDistribuAll({arr:JSON.stringify(request)})
      if (result.data.flag) {
        this.$Message.success("操作成功");
        this.popupCancel()
        this.getList()
      } else {
        this.$Message.error(result.data.message);
      }
    },
    async setDistribOne(){
      let params = {
        entity_caseId:this.requsetIds,
        second_secretary:'',
        second_secretary_name:"",
        actionIdentifer:'CASE_ASSIGN'
        }
      const arr = this.personTable.filter(item=>{
         if(item.checked){
           return item
        } else {
          return 
        }
      })
      if(arr.length>0){
        params.second_secretary= arr[0].id
         params.second_secretary_name= arr[0].nickname
      }
      const result = await this.setDistribution(params);
      if (result.data.flag) {
        this.$Message.success("操作成功");
        this.popupCancel()
        this.getList()
      } else {
        this.$Message.error(result.data.message);
      }
    },
    // 弹窗取消
    popupCancel () {
      console.log(1)
      this.popupShow = false
      this.requsetIds='' // 重置
    },
    // 选择
    handleCheckbox (row, index) {
      let copy = JSON.parse(JSON.stringify(this.personTable));
      if(row.checked){
        copy.map(item=>{
          item.checked=false
        })
        copy[index]=row;
        this.personTable=copy;
      }
    },
    async getList () {
      let params = {
        menuIdentifer: "CASE_ASSIGN_LIST", //菜单标识
        pageIndex: this.currentPage - 1,
        pageSize: this.pageSize
      };
      let filter ={//筛选条件
        applicantName:"",//申请人关键字
        respondentName:"",//被申请人关键字
        userId:'' ,//注册名称的用户id
        caseTypeId: '',//案件类型id
    	  createTimeBegin:"",//提交的开始时间
        createTimeEnd:"" //提交的结束时间
    }

      params = { ...params,filter:JSON.stringify(filter) };
      const result = await this.getRequsetList(params)
      if (result.data.flag) {
        console.log('待立案list',result.data.data);
        this.tableData = result.data.data.dataList;
        this.total = result.data.data.totalCount;
        this.selectedData=[] // 重置
      }
    },
    async getAllPerson(){
      const result = await this.gelAllArbitrator()
      if(result.data.flag){
        this.personTable=result.data.data
      }
    }
  }
}
</script>
<style lang="less">
.ditribute_case {
  .content {
    margin-top: 30px;
    padding: 20px 10px;
    background: #fff;
    border-radius:4px;
    .buttons {
      padding-right: 20px;
      text-align: right;
    }
    .table_box {
      // padding: 0 2px;
    }
  }
  .mgt20 {
    margin-top: 20px;
  }
  .mgl10 {
    margin-left: 10px;
  }
}
.select_content {
  .info {
    text-align: center;
  }
  .header_box {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .file_table{
    margin: 10px 0 ;
  }
}
</style>
<style lang="less">
@import url('../../../style/iview_table.less');
</style>