export const listMixin = {
  data() {
      return {
          currentPage: 1,
          pageSize: 10,
          total: 0,
          pageOpts:[10,20,50],
          tableData:[], // list数据
          selectedData: [],// 复选框选中的数据
          caseStatus:{ // 案件状态
            1:'待受理',
            2:"已驳回",
            3:"待缴费",
            4:"待立案",
            5:"立案审核中",
            6:"立案成功",
            7:'待组庭',
            8:'待指定仲裁员',
            9:'待确认或修改仲裁员',
            10:"待选择审理方式",
            11:"待开庭",
            12:'待结案'
          }
      }
  },
  mounted() {
      this.getList()
  },
  methods: {
      selectOne(selection, row) {
          this.selectedData.push(row.id)
      },
      selectCancel(selection, row) {
          let id = row.id;
          let arr = this.selectedData
          if (arr.indexOf(id) > -1) {
              arr.splice(arr.indexOf(id), 1)
          }
      },
      selectAll(selection) {
          // console.log('全选', selection)
          this.selectedData.push(...selection.map(item => item.id));
          // 去重
          let copy = JSON.parse(JSON.stringify(this.selectedData))
          this.selectedData = Array.from(new Set(copy))
      },
      selectAllCancel(selection) {
          // console.log('反选', selection)
          const ids = this.tableData.map(v => v.id);
          let copy = JSON.parse(JSON.stringify(this.selectedData))
          ids.map(item => {
              console.log(copy.includes(item))
              if (copy.includes(item)) {
                  copy.splice(copy.indexOf(item), 1)
              }
          })
          this.selectedData = copy
      },
      changeCurrentPage(value) {
          // console.log('当前页', value);
          this.currentPage = value;
          this.getList();
      },
      changeSize(value) {
          // console.log('当前大小', value)
          this.pageSize = value;
          this.getList();
      },
  },
}